
.music-play-list {
			position: absolute;
			bottom: 75px;
			width: 230px;
			height: auto;
			/*top:1px;*/
			-webkit-transform: translate(-230px);
			        transform: translate(-230px);
			z-index: 100;
			overflow: hidden;
			border: 1px solid gray;
			-webkit-box-sizing: border-box;
			        box-sizing: border-box;
			background: rgba(50,50,50,1);
			-webkit-transition: -webkit-transform .3s;
			transition: -webkit-transform .3s;
			transition: transform .3s;
			transition: transform .3s, -webkit-transform .3s;
			-webkit-box-shadow: 2px 0 2px rgba(0, 0, 0, 0.5), inset 0 0 5px rgba(255, 255, 255, 0.2);
			        box-shadow: 2px 0 2px rgba(0, 0, 0, 0.5), inset 0 0 5px rgba(255, 255, 255, 0.2);
}
.play-list-header {
		width: 100%;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		padding: 15px;
    font-size: 20px;
    color: #888;
    text-shadow: 0 0 2px rgba(0, 0, 0, 0.9);
    border-bottom: 1px solid #202020;
    -webkit-box-shadow: 0 0 5px #000, inset 0 0 5px rgba(255, 255, 255, 0.2);
            box-shadow: 0 0 5px #000, inset 0 0 5px rgba(255, 255, 255, 0.2);
    text-align: center;
    background-color: #404040;
    cursor: default;
		position: absolute;
		z-index: 99;
}
.play-list-ul {
		width: 100%;
    overflow-y: hidden;
    overflow-x: hidden;
    opacity: 0.8;
    /*max-height: calc(100% - 60px);*/
		height: auto;
		position: absolute;
		-webkit-transition: top .3s;
		transition: top .3s;
		top: 60px;
		z-index: 50;
}
.play-list-ul li {
		border-bottom: 1px solid #555;
    padding: 15px;
    color: #888;
    font-size: 15px;
    width: 100%;
		/*height: 100%;*/
    cursor: pointer;
    word-break: break-all;
    -webkit-transition: background-color 0.2s;
    transition: background-color 0.2s;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-shadow: 0 0 2px rgba(0, 0, 0, 0.7);
		z-index: 50;
}
.play-list-ul li span {
		float: left;
		font-size: 16px;
}
.play-list-ul li:hover {
		background-color: #aaa;
    color: #fff;
    -webkit-box-shadow: inset -1px 0 2px rgba(255, 255, 255, 0.8);
            box-shadow: inset -1px 0 2px rgba(255, 255, 255, 0.8);
}
.play-list-ul .playing {
		color: #fff;
    background-color: #30ABD5 !important;
    -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 1) !important;
            box-shadow: inset 0 0 3px rgba(0, 0, 0, 1) !important;
}
.music {
		height: 75px;
		left: 0;
		width: 100%;
		background: rgba(91, 91, 91, 0.4);
		color: white;
		text-align: center;
		position: fixed;
		bottom:0;
		z-index: 999;
}
.music-my {
		position: absolute;
		bottom: 75px;
		width:700px;
		height: 610px;
		left: -200%;
		margin-left: -350px;
		background: rgba(110, 168, 245, 0.8);
		-webkit-transition: all .6s ease-out;
		transition: all .6s ease-out;
		font-family: 'KaiTi';
		color:white;
		overflow: hidden;
}
.music-myname {
		font-size: 24px;
		width: 100%;
		padding: 0 5%;
		/*margin:0 auto;*/
		height: 40px;
		line-height: 40px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		background: rgb(25,60,120);
		position: relative;
		z-index: 99;
		font-family: 'KaiTi';
		-webkit-touch-callout: none; /* iOS Safari */
		-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
		-moz-user-select: none; /* Firefox */
		-ms-user-select: none; /* Internet Explorer/Edge */
		user-select: none; /* Non-prefixed version, currently*/
}
.music-begin {
		float: left;
		margin-left: 8%;
		cursor: pointer;
		font-family: 'KaiTi';
}
.music-begin a {
		display: none;
}
.music-No {
		width: 35px;
		position: absolute;
		left: 10px;
}
.music-down {
		float: left;
		position: absolute;
		right: 21px;
}
.music-down a {
		color: rgba(255,255,255,0.7);
}
.music-down a:hover {
		color: orange;
}
.music-line {
		position: absolute;
		right: 60px;
		color: white!important;
}
.music-begin:hover {
		color:orange!important;
}
.music-text {
		float: left;
		width:295px;
		height:100%;
		text-align: center;
}
.music-master {
		float: left;
		width:200px;
		height:100%;
		text-align: center;
		margin-left: 2%;
}
.music-ul {
		width: 700px;
		height: auto;
		/*overflow: hidden;*/
		position: relative;
		z-index: 10;
		-webkit-touch-callout: none; /* iOS Safari */
		-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
		-moz-user-select: none; /* Firefox */
		-ms-user-select: none; /* Internet Explorer/Edge */
		user-select: none; /* Non-prefixed version, currently*/
		/*box-sizing: border-box;*/
}
.music-scroll {
		position: absolute;
		width:15px;
		height:570px;
		right:0px;
		top:40px;
		background: #eaeaea;
		z-index: 99;
}
.music-scrollbar {
		position: absolute;
		width:100%;
		height:80px;
		top:0;
		left:0;
		background: rgba(55, 114, 246,0.8);
		cursor: pointer;
		-webkit-transition: color .3s;
		transition: color .3s;
}
.music-scrollbar:hover {
		background: rgba(55, 114, 246,0.9);
}
.music-li {
		width:100%;
		height:40px;
		line-height: 40px;
		font-size: 16px;
		padding-left: 30px;
		position: relative;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		font-family: 'KaiTi';
}
.music audio {
		width:300px;
		height:60px;
}
.music-wrap {
		width:910px;
		height:40px;
		position: absolute;
		top:50%;
		left:50%;
		margin-left: -455px;
		margin-top: -20px;
}
.music-btn {
		width: 135px;
		height:100%;
		float: left;
}
.music .c-btn {
		background: url('../assets/player.png');
		display: block;
		float: left;
		width:19px;
		height:20px;
		margin-top: 10px;
		cursor: pointer;
		opacity: 0.8;
		-webkit-transition: opacity .2s;
		transition: opacity .2s;
		/*position: absolute;*/
}
.music .c-btn:hover {
		opacity: 1;
}
.music .prev {
		background-position: 0 -30px;
		margin-right: 32px;
}
.music .play {
		width:21px;
		height:29px;
		background-position: 0 0;
		margin-top: 6px;
		margin-right: 30px;
}
.music .pause {
		width:21px;
		height:29px;
		background-position: -30px 0;
		margin-top: 6px;
		margin-right: 30px;
}
.music .next {
		background-position: 0 -52px;
}
.music-show {
		width:450px;
		height:100%;
		float: left;
		margin-left: 25px;
		margin-top: -5px;
		/*position: relative;*/
		-webkit-user-select: none;
		   -moz-user-select: none;
		    -ms-user-select: none;
		        user-select: none;
}
.music-name {
		font-size: 16px;
		text-align: left;
		margin-bottom: 5px;
		bottom:5px;
		font-family: 'KaiTi';
		position: relative;
		-webkit-touch-callout: none; /* iOS Safari */
		-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
		-moz-user-select: none; /* Firefox */
		-ms-user-select: none; /* Internet Explorer/Edge */
		user-select: none; /* Non-prefixed version, currently*/
}
.music-time {
		position: absolute;
		right:0;
		top:0;
		z-index: 100;
}
.music-show-name {
		z-index: 60;
		max-width: 350px;
		height: 30px;
		word-break:keep-all;/* 不换行 */
		white-space:nowrap;/* 不换行 */
		overflow:hidden;/* 内容超出宽度时隐藏超出部分的内容 */
		text-overflow:ellipsis;/* 当对象内文本溢出时显示省略标记(...) ；需与overflow:hidden;一起使用。*/
}
.music-time span {
		font-family: 'KaiTi','-webkit-pictograph';
		font-size: 17px;
}
.music-bar {
		width:450px;
		height:2px;
		bottom:-5px;
		background: rgba(135, 129, 130,0.8);
		position: absolute;
		bottom: 5px;
		cursor: pointer;
}
.music-plan {
		position: absolute;
		width:0;
		height:2px;
		background: white;
}
.music-drag {
		position: absolute;
		width:12px;
		height:12px;
		border-radius: 50%;
		background: white;
		left:0;
		top:-5px;
		cursor: pointer;
}
.music-order {
		float: left;
		height:100%;
		width:35px;
		margin-left: 25px;
}
.music-order .order-play {
		display: block;
		width:23px;
		height:20px;
		background-position: 0 -260px;
}
.music-order .random-play {
		display: block;
		width:25px;
		height:19px;
		background-position: 0 -74px;
}
.music-order .one-play {
		display: block;
		width:26px;
		height:25px;
		background-position: 0 -232px;
}
.music-order .list-play {
		display: block;
		width:26px;
		height:25px;
		background-position: 0 -205px;
}
.music-vol {
		width:130px;
		height:100%;
		float: left;
		margin-left: 10px;
		margin-top: -1px;
}
.music-vol .vol-btn {
		width:26px;
		height:21px;
		background-position: 0 -144px;
		margin-top: 12px;
		float: left;
}
.music-vol .vol-close {
		width:26px;
		height:21px;
		background-position: 0 -182px;
		margin-top: 12px;
		float: left;
}
.music-vol .vol-bar {
		position: relative;
		width:90px;
		height:2px;
		background: rgba(135, 129, 130,0.8);
		float: left;
		margin-top: 20px;
		margin-left: 5px;
		cursor: pointer;
}
.vol-bar .vol-plan {
		width:90px;
		height:2px;
		background-color: white;
		position: absolute;
}
.music-vol .vol-bar-btn {
		position: absolute;
		width:12px;
		height:12px;
		border-radius: 50%;
		background: white;
		top:-5px;
		left:90px;
		cursor: pointer;
}
.music-list {
		width:59px;
		height:36px;
		float: left;
		margin-left: 20px;
}
.music-list-img {
		background: url('http://ogm5at7ve.bkt.clouddn.com/playbar.png');
		background-position: -42px -68px;
		float: none;
		width:38px;
		text-align: center;
		color:rgba(255,255,255,0.8);
		display: block;
		height:25px;
		margin: 5px 0 0 10px;
		padding-left: 21px;
		font-size: 15px;
		line-height: 26px;
		cursor: pointer;
		-webkit-transition: color .2s;
		transition: color .2s;
		font-family: 'KaiTi';
		-webkit-touch-callout: none; /* iOS Safari */
		-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
		-moz-user-select: none; /* Firefox */
		-ms-user-select: none; /* Internet Explorer/Edge */
		user-select: none; /* Non-prefixed version, currently*/
}
.music-list-img:hover {
		 background-position: -42px -98px;
		 color: white;
}



